* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f4f7f6;
  color: #333;
}

.section {
  padding: 60px 0;
  text-align: center;
}

.profile-photo {
  width: 200px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.skills-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.skill-item {
  margin: 10px;
  text-align: center;
}

.skill-item img {
  width: 50px;
  height: 50px;
}

.experience-item, .project-item {
  margin-bottom: 20px;
}

footer {
  background-color: #1f3b6e;
  color: white;
  padding: 20px;
}
